import React, { Component } from 'react';
import PropTypes from 'prop-types';
import muteonBtnImg from '../../assets/MuteBtn/Mute-Mic-on.svg'
import muteoffBtnImg from '../../assets/MuteBtn/Mute-Mic-off.png'

import cameraonBtnImg from '../../assets/CameraBtn/CameraBtn-on.svg'
import cameraoffBtnImg from '../../assets/CameraBtn/CameraBtn-off.png'

import shareonBtnImg from '../../assets/ShareBtn/Share-icon-on.svg'
import shareoffBtnImg from '../../assets/ShareBtn/Share-icon-off.png'

import raiseonBtnImg from '../../assets/RaiseBtn/Raise-Hand-on.svg'
import raiseoffBtnImg from '../../assets/RaiseBtn/Raise-Hand-off.png'

import './control.css'

class Controls extends Component {

  state = {
    muted: false,
    focus: false,
    videoStatus: 'Disabled',
    handRaiseStatus: true,
    shareScreenStatue: true
  };

  callback = (localMuted) => {
    this.setState({ muted: localMuted });
  }

  componentDidMount() {
    this.props.chime.audioVideo.realtimeSubscribeToMuteAndUnmuteLocalAudio(
      this.callback
    );
  }

  componentWillUnmount() {
    if (this.props && this.props.chime.audioVideo) {
      this.props.chime.audioVideo.realtimeUnsubscribeToMuteAndUnmuteLocalAudio(
        this.callback
      );
    }
  }

  muteButtonOnClick = async () => {
    if (this.state.muted) {
      this.props.chime.audioVideo.realtimeUnmuteLocalAudio();
    } else {
      this.props.chime.audioVideo.realtimeMuteLocalAudio();
    }
    await new Promise(resolve => setTimeout(resolve, 10));
  }

  raiseButtonOnClick = async () => {
    if (this.state.handRaiseStatus === false) {
      this.setState({ handRaiseStatus: true });
    }
    else {
      this.setState({ handRaiseStatus: false });
    }
  }

  shareButtonOnClick = async () => {
    if (this.state.shareScreenStatue === false) {
      this.setState({ shareScreenStatue: true });
    }
    else {
      this.setState({ shareScreenStatue: false });
    }
  }

  videoButtonOnClick = async () => {
    await new Promise(resolve => setTimeout(resolve, 10));
    if (this.state.videoStatus === 'Disabled') {
      this.setState({ videoStatus: 'Loading' });
      try {
        if (!this.props.chime.currentVideoInputDevice) {
          throw new Error('currentVideoInputDevice does not exist');
        }

        try {
          await this.props.chime.chooseVideoInputDevice(
            this.props.chime.currentVideoInputDevice
          );
        } catch (err) {
          const videoInputDevices = await this.props.chime.audioVideo.listVideoInputDevices();
          await this.props.chime.audioVideo.chooseVideoInputDevice(videoInputDevices[0].deviceId);
        }

        this.props.chime.audioVideo.startLocalVideoTile();

        this.setState({ videoStatus: 'Enabled' });
      } catch (error) {
        // eslint-disable-next-line
        console.error(error);
        this.setState({ videoStatus: 'Disabled' });
      }
    } else if (this.state.videoStatus === 'Enabled') {
      this.setState({ videoStatus: 'Loading' });
      this.props.chime.meetingSession.audioVideo.stopLocalVideoTile();
      this.setState({ videoStatus: 'Disabled' });
    }
  }

  endButtonOnClick = async () => {
    await this.props.chime.leaveRoom(this.props.role === 'host');
    sessionStorage.removeItem(this.props.ssName);
    const whereTo = `${this.props.baseHref}/${this.props.role === 'host' ? '' : 'join?room=' + this.props.title}`;
    this.props.history.push(whereTo);
  }

  render() {
    const mic_controls = this.state.muted ? 'controls__btn--mic_on' : 'controls__btn--mic_off';
    const cam_controls = this.state.videoStatus === 'Enabled' ? 'controls__btn--cam_off' : 'controls__btn--cam_on';
    const hand_controls = this.state.handRaiseStatus ? 'controls__btn--raise-on' : 'controls__btn--raise-off';
    const share_controls = this.state.shareScreenStatue ? 'controls__btn--share-on' : 'controls__btn--share-off';
    return (
      <div className="controls pos-relative">
        <div className="button-group1">
          <button className={`controls__btn ${mic_controls} btn rounded btn--mic`} onClick={this.muteButtonOnClick}>
            <div className="btn__svg btn__svg--mic_on button-layout btn-toggle" >
              <img src={this.state.muted ? muteoffBtnImg : muteonBtnImg} alt="mute icon" className={'toggle-icon'}></img>
              <span className="txt-oncontrolbtn toggle-text">{this.state.muted ? 'Unmute' : 'Mute'}</span>
            </div>
          </button>

          <button className={`controls__btn ${cam_controls} btn rounded btn--cam`} onClick={this.videoButtonOnClick}>
            <div className="btn__svg btn__svg--cam_on button-layout btn-toggle">
              <img src={this.state.videoStatus === 'Enabled' ? cameraonBtnImg : cameraoffBtnImg} alt="camera icon" className={'toggle-icon'}></img>
              <span className="txt-oncontrolbtn toggle-text">{this.state.videoStatus === 'Enabled' ? 'Camera on' : 'Camera off'}</span>
            </div>
          </button>
        </div>
        <div className="button-group2">
          <button className={`controls__btn ${share_controls} btn rounded btn--share`} onClick={this.shareButtonOnClick}>
            <div className="btn__svg btn__svg--share_on button-layout btn-toggle">
              <img src={this.state.shareScreenStatue ? shareonBtnImg : shareoffBtnImg} alt="share icon" className={'toggle-icon'}></img>
              <span className="txt-oncontrolbtn toggle-text">{this.state.shareScreenStatue ? 'Share screen' : 'Stop sharing'}</span>
            </div>
          </button>
          <button className={`controls__btn ${hand_controls} btn rounded btn--hand`} onClick={this.raiseButtonOnClick}>
            <div className="btn__svg btn__svg--hand_on button-layout btn-toggle">
              <img src={this.state.handRaiseStatus ? raiseonBtnImg : raiseoffBtnImg} alt="raise icon" className={'toggle-icon raise-toggle-icon'}></img>
              <span className="txt-oncontrolbtn toggle-text">{this.state.handRaiseStatus ? 'Raise hand' : 'Lower hand'}</span>
            </div>
            
          </button>
        </div>
        <div className="button-group3">
          <button className="go-livebtn" onClick={this.props.openSettings}>
            <span className="txt-onbtn">GO LIVE</span>
          </button>
          <button className="live-meetingbtn txt-onbtn" onClick={this.endButtonOnClick}>
            <span className="txt-onbtn">LEAVE MEETING</span>
          </button>
        </div>
      </div>
    )
  }
}

Controls.propTypes = {
  chime: PropTypes.object,
  baseHref: PropTypes.string,
  ssName: PropTypes.string,
  title: PropTypes.string,
  openSettings: PropTypes.func,
  role: PropTypes.string,
  history: PropTypes.object,
  myVideoElement: PropTypes.object
};

export default Controls;
